@import url(https://fonts.googleapis.com/css2?family=Inter&display=swap);
.background {
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center center;
    text-align: center;
    font-size: 20px;
    margin-top: 1rem;
    padding: 0.3rem;
}
.activeContainer {
    background-image: url(/millionaries-game/static/media/activeElement.b9d670d4.svg);
    color: #FF8B37;
}
.inActiveContainer {
    background-image: url(/millionaries-game/static/media/inActiveElement.cccee0e5.svg);
    color: #D0D0D8;
}
.nextContainer {
    background-image: url(/millionaries-game/static/media/nextElement.cccee0e5.svg);
    color: #1C1C21;
}
.button1 {
    float: left;
    width: 25rem;
    margin: 2rem 0 0 2rem;
    background-image: url(/millionaries-game/static/media/btnInActive.95521e72.svg);
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center center;
    text-align: center;
    line-height: 72px;
    font-size: 20px;
    font-family: Inter, sans-serif;
    cursor: pointer;
    outline: none;
    transition: all 0.2s;
}

.button1:hover {
    background-image: url(/millionaries-game/static/media/btnHover.b4ae8a75.svg);
}

.button1:active {
    background-image: url(/millionaries-game/static/media/btnActive.bc63c554.svg);
}

.false {
    background-image: url(/millionaries-game/static/media/btnFalse.b1b24eb5.svg);
}
.true {
    background-image: url(/millionaries-game/static/media/btnTrue.a5b9c731.svg);
}
.score {
    font-size: 32px;
    font-weight: 600;
    color: #1C1C21;
    opacity: 0.5;
}
.hand {
    background-image: url(/millionaries-game/static/media/hand.97255040.svg);
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center center;
    width: 400px;
    height: 400px;
}
.price {
    color: #1C1C21;
    font-size: 56px;
    font-weight: 600;
}
.startBtn {
    cursor: pointer;
}
/*body {*/
/*    background-color: #E5E5E5;*/
/*}*/
.board {
    display: flex;
    flex-wrap: wrap;
    position: relative;
    height: 100vh;
    background-color: #E5E5E5;
}
.question {
    padding: 2rem 0 0 3rem;
    width: 69.5vw;
    min-height: 30vh;
}
.answers {
    width: 70vw;
    /*height: 40vh;*/
    padding-bottom: 2rem;
}
.menu {
    position: absolute;
    right: 0;
    top: 0;
    width:29vw;
    background-color: white;
    height: 100vh;
}

.button {
    position: absolute;
    left: 16px;
    right: 16px;
    top: 0;
    bottom: 0;
    background: #FFFFFF;
    border: 1px solid #D0D0D8;
    box-sizing: border-box;
}
body {
    width: 100vw;
    /*height: 100%;*/
    background: linear-gradient(155.1deg, rgba(255,255,255,1) 50%, rgba(255,243,235,1) 50%) no-repeat;
    overflow-x: hidden;
}
.main {
    margin-top: 5rem;
}
.img {
    background-image: url(/millionaries-game/static/media/hand.97255040.svg);
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center center;
    width: 400px;
    height: 400px;
}
.mainText {
    position: static;
    width: 624px;
    height: 130px;
    left: 0px;
    top: 0px;

    /* Desktop / Headline */

    font-family: Inter, sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 56px;
    line-height: 116%;
    /* or 65px */

    display: flex;
    align-items: center;

    /* Black-100 */

    color: #1C1C21;

    /* Inside Auto Layout */

    flex: none;
    order: 0;
    align-self: center;
    flex-grow: 0;
    margin: 2rem 0;
}
.startBtn {
    width: 296px;
    height: 64px;
    border-radius: 12px;
    background-color: #FF8B37;
    outline: none;
    border: none;
    transition: all 0.2s;
}
.textBtn {
    font-weight: bold;
    color: white;
}
.startBtn:hover {
    background-color: #FFAC70;
}
.startBtn:active {
    background-color: #E87928;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', Inter,
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

